body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Button {
  border: 1px solid #521751;
  font: inherit;
  padding: 8px;
  background-color: #521751;
  color: white;
  cursor: pointer;
}

.Button:hover,
.Button:active {
  background-color: #fa923f;
}
.App {
  text-align: center;
}
.Modal {
    position: fixed;
    z-index: 200;
    border: 1px solid #eee;
    box-shadow: 0 2px 2px #ccc;
    background-color: white;
    padding: 10px;
    text-align: center;
    box-sizing: border-box;
    top: 30%;
    left: 25%;
    width: 50%;
    transition: all 0.3s ease-out;
}

.ModalOpen  {
    -webkit-animation: openModal 0.4s ease-out forwards;
            animation: openModal 0.4s ease-out forwards;
}

.ModalClosed{
    -webkit-animation: closeModal 1s ease-out forwards;
            animation: closeModal 1s ease-out forwards;
}

.fade-slide-enter {

}

.fade-slide-enter-active {
    -webkit-animation: openModal 0.4s ease-out forwards;
            animation: openModal 0.4s ease-out forwards;

}

.fade-slide-exit {

}

.fade-slide-exit-active {
        -webkit-animation: closeModal 1s ease-out forwards;
                animation: closeModal 1s ease-out forwards;
}

@-webkit-keyframes openModal {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%)
    }
    50% {
        opacity: 1;
        -webkit-transform: translateY(90%);
                transform: translateY(90%)
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}

@keyframes openModal {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%)
    }
    50% {
        opacity: 1;
        -webkit-transform: translateY(90%);
                transform: translateY(90%)
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}

@-webkit-keyframes closeModal {
    0% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%)
    }

    50% {
        opacity: 0.8;
        -webkit-transform: translateY(60%);
                transform: translateY(60%)
    }

    100% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%)
    }
}

@keyframes closeModal {
    0% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%)
    }

    50% {
        opacity: 0.8;
        -webkit-transform: translateY(60%);
                transform: translateY(60%)
    }

    100% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%)
    }
}
.Backdrop {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.8);
}

.BackdropOpen {
    display: block;
}

.BackdropClosed {
    display: none;
}
.List {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    width: 280px;
}

.ListItem {
    margin: 0;
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    border: 1px solid #521751;
    background-color: white;
    text-align: center;
    cursor: pointer;
}

.ListItem:hover,
.ListItem:active {
    background-color: #ccc;
}

.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    opacity: 1;
    transition: opacity 0.3s ease-out
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0;
    transition: opacity 0.3s ease-out

}
